	module.exports = {"data":{"markdownRemark":{"html":"<h2>A Tag</h2>\n<p>The <code>&#x3C;a></code> tag or <em>anchor</em> element creates a hyperlink to another page or file. In order to link to a different page or file the <code>&#x3C;a></code> tag must also contain a <code>href</code> attribute, which indicates the link’s destination.</p>\n<p>The text between the opening and closing <code>&#x3C;a></code> tags becomes the link.</p>\n<p>By default, a linked page is displayed in the current browser window unless another target is specified. </p>\n<h4>Example:</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code>  &lt;a href= \"https://guide.freecodecamp.org/\">freeCodeCamp<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>a</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>An image can also be turned into a link by enclosing the <code>&#x3C;img></code> tag in an <code>&#x3C;a></code> tag.</p>\n<h4>Example:</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code>  &lt;a href= \"https://guide.freecodecamp.org/\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>img</span> <span class=\"token attr-name\">src</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>logo.svg<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>a</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<ul>\n<li><a href='https://developer.mozilla.org/en-US/docs/Web/HTML/Element/a' target='_blank' rel='nofollow'>The HTML &#x3C;a> element: MDN</a></li>\n<li><a href='https://www.w3schools.com/tags/tag_a.asp' target='_blank' rel='nofollow'>A tag: w3schools</a></li>\n</ul>","fields":{"slug":"/html/elements/a-tag/"},"frontmatter":{"title":"A Tag"}}},"pathContext":{"slug":"/html/elements/a-tag/"}}

